}

.header__switch-input {
  display: none;
}

.header__switch-input:checked + .header__slider:before {
  background-color: #f7f7f7;
  transform: translateX(19.5px);
}

.header__switch-input:checked + .header__slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

.header__slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  flex-shrink: 0;
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.header__slider:before {
  content: "";
  background-color: #fff;
  background-position: center;
  background-size: cover;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.header__round {
  border-radius: 34px;
}

.header__round:before {
  border-radius: 50%;
}

